(function() {
  'use strict';

  angular
    .module('lhr70')
    .controller('StoryController', StoryController);

  /** @ngInject */
  function StoryController(story, $stateParams) {
    var vm = this;

    var id = $stateParams.id;

    vm.story = story.getStoryById(id);
  }
})();

