(function () {
  'use strict';

  angular
    .module('lhr70')
    .directive('storyBlock', storyBlock);

  /** @ngInject */
  function storyBlock() {
    var directive = {
      restrict: 'EA',
      templateUrl: 'app/components/story/story.directive.html',
      scope: {
        story: '='
      },
      controller: StoryController,
      controllerAs: 'vm',
      bindToController: true
    };

    /** @ngInject */
    function StoryController($log) {
      var vm = this;
      vm.image = 'https://unsplash.it/600?image=' + Math.floor((Math.random() * 100) + 1);
      $log.log(vm);
    }

    return directive;
  }
})();

